<template>
  <div @click="showSnippet" class="menu-item">
    {{snippet.name}}
  </div>
</template>

<script>
export default {
  name: "MenuItem",
  props: ['snippet'],
  methods: {
      showSnippet: async function() {
          this.$store.dispatch('showSnippet', this.snippet)
      }
  }
  
};
</script>
