<template>
  <div class="menu">
    <div v-for="snippet of snippets" v-bind:key="snippet.id">
      <menu-item :snippet="snippet"></menu-item>
    </div>
  </div>
</template>

<script>

import MenuItem from './menu/MenuItem.vue';

export default {
  name: "Menu",
  components: {
    MenuItem
  },
  computed: {
    snippets() {
      return this.$store.getters.snippets;
    }
  }
};
</script>
